*, *:before, *:after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
.icon {
	background-color: transparent;
	border: 0;
	color: #d4f1f9 ;
	cursor: pointer;
	display: block;
	font-size: 20px;
	margin: 3em auto 0 auto;
	position: relative;
	width: 9em;
	height: 9em;
	-webkit-appearance: none;
	appearance: none;
}
.icon div {
	position: absolute;
}
.cloud {
	transition: opacity 0.1s linear;
}
/* Cloud */
.cloud {
	top: 0;
	width: 9em;
	height: 5.8em;
}
.puff, .cloud-core {
	background-color: #87CEEB;
}
.puff {
	border-radius: 50%;
}
.puff-1 {
	top: 2.5em;
	left: 0;
	width: 3.3em;
	height: 3.3em;
}
.puff-2 {
	top: 1em;
	left: 1.2em;
	width: 3em;
	height: 3em;
}
.puff-3 {
	top: 0;
	left: 3em;
	width: 4.6em;
	height: 4.6em;
}
.puff-4 {
	top: 1.8em;
	left: 5em;
	width: 4em;
	height: 4em;
}
.puff-5 {
	top: 2.3em;
	left: 2.4em;
	width: 3.5em;
	height: 3.5em;
}
.puff-6 {
	top: 2.3em;
	left: 3em;
	width: 3.5em;
	height: 3.5em;
}
.puff-7 {
	top: 2.4em;
	left: 1em;
	width: 1.8em;
	height: 1.8em;
}
.puff-8 {
	top: 1.2em;
	left: 2.5em;
	width: 2em;
	height: 2em;
}
.puff-9 {
	top: 1.8em;
	left: 5.5em;
	width: 2em;
	height: 2em;
}
.puff-10 {
	top: 3.6em;
	left: 3.5em;
	width: 2.2em;
	height: 2.2em;
}
.cloud-core {
	top: 1.8em;
	left: 1.8em;
	width: 5em;
	height: 4em;
}
.check {
	top: 0;
	left: 1.6em;
	width: 5.8em;
	height: 5.8em;
	position: relative;
	-webkit-transform: scale(0);
	        transform: scale(0);
	z-index: -1;
}
.check:before, .check:after {
	background-color: currentColor;
	content: "";
	bottom: 0;
	display: block;
	left: 2.5em;
	position: absolute;
	width: 0.8em;
}
.check:before {
	height: 3em;
	-webkit-transform: rotate(-55deg);
	        transform: rotate(-55deg);
	-webkit-transform-origin: 50% 2.6em;
	        transform-origin: 50% 2.6em;
}
.check:after {
	height: 5.5em;
	-webkit-transform: rotate(35deg);
	        transform: rotate(35deg);
	-webkit-transform-origin: 50% 5.1em;
	        transform-origin: 50% 5.1em;
}
/* Arrow */
.arrow {
	top: 3em;
	left: 0;
	width: 9em;
	height: 5.3em;
}
.arrow div, .progress {
	border-radius: 0.4em;
}
.arrow div {
	background-color: currentColor;
}
.arrow-stem {
	bottom: 0;
	left: 4.1em;
	width: 0.8em;
	height: 5.3em;
	-webkit-transform-origin: 50% 100%;
	        transform-origin: 50% 100%;
}
.arrow-l-tip, .arrow-r-tip {
	left: calc(50% - 0.4em);
	bottom: 0;
	width: 2.6em;
	height: 0.8em;
	-webkit-transform-origin: 0.4em 50%;
	        transform-origin: 0.4em 50%;
}
.arrow-l-tip {
	-webkit-transform: rotate(-135deg);
	        transform: rotate(-135deg);
}
.arrow-r-tip {
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.progress {
	background-color: #fff;
	bottom: 0;
	left: 0;
	width: 0;
	height: 0.8em;
}

/* Droplets */
.preload, .drop {
	transition: all 0.2s linear;
}
.preload {
	-webkit-animation: spin 1s linear infinite;
	        animation: spin 1s linear infinite;
	opacity: 0;
	top: 2.9em;
	left: calc(50% - 1.25em);
	width: 2.5em;
	height: 2.5em;
	z-index: 1;
}
.drop {
	background-color: currentColor;
	background-image: radial-gradient(0.5em 0.5em at 30% 75%,rgb(255,255,255) 45%, rgba(255,255,255,0) 50%);
	border-radius: 50%;
	width: 100%;
	height: 100%;
}
.drop-1 {
	-webkit-transform: scaleY(0.75) rotate(135deg);
	        transform: scaleY(0.75) rotate(135deg);
}
.drop-2 {
	-webkit-transform: rotate(120deg) scaleY(0.75) rotate(135deg);
	        transform: rotate(120deg) scaleY(0.75) rotate(135deg);
}
.drop-3 {
	-webkit-transform: rotate(240deg) scaleY(0.75) rotate(135deg);
	        transform: rotate(240deg) scaleY(0.75) rotate(135deg);
}

/** Animation **/
.waiting, .running {
	cursor: default;
}
/* Waiting */
.waiting .cloud {
	opacity: 0.15;
}
.waiting .preload {
	opacity: 1;
}
.waiting .drop {
	border-radius: 0 50% 50% 50%;
}
.waiting .drop-1 {
	-webkit-transform: translateY(2.5em) scaleY(0.75) rotate(135deg);
	        transform: translateY(2.5em) scaleY(0.75) rotate(135deg);
}
.waiting .drop-2 {
	-webkit-transform: rotate(120deg) translateY(2.5em) scaleY(0.75) rotate(135deg);
	        transform: rotate(120deg) translateY(2.5em) scaleY(0.75) rotate(135deg);
}
.waiting .drop-3 {
	-webkit-transform: rotate(240deg) translateY(2.5em) scaleY(0.75) rotate(135deg);
	        transform: rotate(240deg) translateY(2.5em) scaleY(0.75) rotate(135deg);
}

/* Running */
.running .puff {
	-webkit-animation-duration: 0.75s;
	        animation-duration: 0.75s;
	-webkit-animation-delay: 2.75s;
	        animation-delay: 2.75s;
	-webkit-animation-timing-function: cubic-bezier(.1,.8,.2,.95);
	        animation-timing-function: cubic-bezier(.1,.8,.2,.95);
}
.running .puff-1 {
	-webkit-animation-name: puff1;
	        animation-name: puff1;
}
.running .puff-2 {
	-webkit-animation-name: puff2;
	        animation-name: puff2;
}
.running .puff-3 {
	-webkit-animation-name: puff3;
	        animation-name: puff3;
}
.running .puff-4 {
	-webkit-animation-name: puff4;
	        animation-name: puff4;
}
.running .puff-5 {
	-webkit-animation-name: puff5;
	        animation-name: puff5;
}
.running .puff-6 {
	-webkit-animation-name: puff6;
	        animation-name: puff6;
}
.running .puff-7 {
	-webkit-animation-name: puff7;
	        animation-name: puff7;
}
.running .puff-8 {
	-webkit-animation-name: puff8;
	        animation-name: puff8;
}
.running .puff-9 {
	-webkit-animation-name: puff9;
	        animation-name: puff9;
}
.running .puff-10 {
	-webkit-animation-name: puff10;
	        animation-name: puff10;
}
.running .cloud-core {
	-webkit-animation: core 2.75s;
	        animation: core 2.75s;
}
.running .check {
	-webkit-animation: check 0.4s 2.75s;
	        animation: check 0.4s 2.75s;
}
.running .arrow {
	-webkit-animation: arrow 0.125s;
	        animation: arrow 0.125s;
}
.running .arrow-stem {
	-webkit-animation: arrowStem 0.125s;
	        animation: arrowStem 0.125s;
}
.running .arrow-l-tip {
	-webkit-animation: arrowLTip 0.375s 0.125s;
	        animation: arrowLTip 0.375s 0.125s;
}
.running .arrow-r-tip {
	-webkit-animation: arrowRTip 0.375s 0.125s;
	        animation: arrowRTip 0.375s 0.125s;
}
.running .progress {
	-webkit-animation: filling 2s 0.75s;
	        animation: filling 2s 0.75s;
}
.icon.running div {
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}
.icon.running .cloud-core, .icon.running .check, .icon.running .arrow, .icon.running .arrow div, .icon.running .progress {
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
}

